body {
  background: #eceeef;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  position: relative;
}

/* Mode Dark */
body.dark-mode {
  background: linear-gradient(to top, rgb(17, 24, 39) 50%, rgb(0, 0, 0) 100%);
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

/** Media Queries **/
h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

/* Header */
.header {
  position: relative;
}
.header__contenedor {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .header__contenedor {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 2px 8px 0 rgba(48, 19, 177, 0.35);
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(3px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    padding-block: 0;
    padding-inline: 6rem;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 20rem;
    margin: 2rem auto;
  }
}
.header__logo {
  order: 2;
}
@media (min-width: 768px) {
  .header__logo {
    order: 0;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    order: 0;
  }
}
.header__heading {
  font-family: "Inspiration", cursive;
  text-align: center;
  margin: 0;
}
.header__heading a {
  color: #543ece;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 4rem;
  }
}

/* Navegacion */
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  order: 3;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    order: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion {
    gap: 4rem;
  }
}
.navegacion__enlace {
  color: #543ece;
  font-size: 2rem;
  padding: 1rem;
  font-weight: 600;
  margin-bottom: 2rem;
  transition-property: background-color color;
  transition-duration: 0.35s;
}
.navegacion__enlace:hover {
  color: #0ddaa6;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    font-size: 1.2rem;
    margin-bottom: 0;
  }
  .navegacion__enlace:hover {
    background-color: #543ece;
    padding: 1rem;
    border-radius: 0.2rem;
    color: #0ddaa6;
  }
}
@media (min-width: 1024px) {
  .navegacion__enlace {
    font-size: 2rem;
  }
  .navegacion__enlace:hover {
    padding: 1.8rem 1rem;
  }
}

/* Toggle */
.toggles {
  align-self: self-end;
  margin-right: 1rem;
  order: 1;
}
@media (min-width: 768px) {
  .toggles {
    align-self: center;
    margin-right: 0;
    order: 0;
  }
}

.toggle {
  position: relative;
  top: 0;
  bottom: 0;
  width: 60px;
  height: 25px;
  margin: 0 auto;
  border-radius: 40px;
}
.toggle [type=checkbox] {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  margin: 0px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}
.toggle span {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  overflow: hidden;
  opacity: 1;
  background-color: #FFFFFF;
  border: 1px solid #543ece;
  border-radius: 40px;
  transition: 0.2s ease background-color, 0.2s ease opacity;
}
.toggle span::before, .toggle span::after {
  content: "";
  position: absolute;
  top: 8px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transition: 0.5s ease transform, 0.2s ease background-color;
}
.toggle span::before {
  background-color: #FFFFFF;
  transform: translate(-58px, 0px);
  z-index: 1;
}
.toggle span::after {
  background-color: #543ece;
  transform: translate(6px, -6px);
  z-index: 0;
}
.toggle input[type=checkbox]:checked + span {
  background-color: #543ece;
}
.toggle input[type=checkbox]:active + span {
  opacity: 0.5;
}
.toggle input[type=checkbox]:checked + span:before {
  background-color: #543ece;
  transform: translate(28px, -10px);
}
.toggle input[type=checkbox]:checked + span:after {
  background-color: #FFFFFF;
  transform: translate(36px, -5px);
}

/* Home */
.home {
  position: relative;
}
@media (min-width: 768px) {
  .home {
    margin-top: 8rem;
    display: grid;
    grid-template-columns: 2fr 1fr;
    height: 50rem;
    align-items: center;
    background-image: url("/assets/img/home/home.webp");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 1rem;
  }
}
@media (min-width: 1024px) {
  .home {
    margin-top: 10rem;
  }
}
.home__heading {
  color: #3b9780;
}
.home__heading a {
  display: block;
  font-size: 5rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  animation: gradient 10s ease-in-out infinite;
  background: linear-gradient(-45deg, #FFFFFF, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 300%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
@keyframes gradient {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
@media (min-width: 768px) {
  .home__grid {
    padding-left: 3rem;
  }
}
@media (min-width: 1024px) {
  .home__grid {
    padding-left: 10rem;
  }
}
.home__typewriter {
  font-family: "Overpass", sans-serif;
  color: #e73c7e;
  font-size: 2.5rem;
  font-weight: 100;
  font-style: italic;
  opacity: 0.8;
  margin-bottom: 20px;
  letter-spacing: 4px;
  border-right: solid 3px #ffffff;
  white-space: nowrap;
  overflow: hidden;
  animation: animated-text 4s steps(29, end) 1s 1 normal both, animated-cursor 600ms steps(29, end) infinite;
  /* Animation */
  /* Cursor Animation */
}
@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 330px;
  }
}
@keyframes animated-cursor {
  from {
    border-right-color: #3b9780;
  }
  to {
    border-right-color: transparent;
  }
}
.home__texto {
  color: #444;
}
@media (min-width: 768px) {
  .home__texto {
    display: block;
    position: absolute;
    left: 10rem;
    right: 10rem;
    animation: 1s ease-in-out 0s 1 mostrarImagen;
    left: 4rem;
    right: 4rem;
    padding-right: 20rem;
    color: #FFFFFF;
  }
  @keyframes mostrarImagen {
    0% {
      opacity: 0;
      transform: translateX(-10rem);
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}
@media (min-width: 1024px) {
  .home__texto {
    display: block;
    position: absolute;
    left: 10rem;
    right: 10rem;
    animation: 1s ease-in-out 0s 1 mostrarImagen;
    padding-right: 30rem;
  }
  @keyframes mostrarImagen {
    0% {
      opacity: 0;
      transform: translateX(-10rem);
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

