    right: 4rem;
    padding-right: 20rem;
    color: #FFFFFF;
  }
  @keyframes mostrarImagen {
    0% {
      opacity: 0;
      transform: translateX(-10rem);
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}
@media (min-width: 1024px) {
  .proyecto__texto {
    display: block;
    position: absolute;
    left: 10rem;
    right: 10rem;
    animation: 1s ease-in-out 0s 1 mostrarImagen;
    padding-right: 30rem;
  }
  @keyframes mostrarImagen {
    0% {
      opacity: 0;
      transform: translateX(-10rem);
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

